.comments__list {
	margin: 0;
	padding: 0;
	list-style-type: none;

	& li {
		padding: 20px;

		&:not(:last-child) {
			border-bottom: 1px solid rgba(0, 0, 0, 0.1);
		}

		& p {
			font-size: 12px;
