				font-size: 16px;
				line-height: 24px;
			}
		}

		& ul {
			position: relative;
			box-sizing: border-box;
			max-width: calc(100% + 20px);
			margin-right: -40px;
			padding-left: 30px;

			@media (min-width: @min-lg) {
				padding-left: 60px;
			}

			& li {
				padding-right: 0;
			}

			&::before {
				content: '';
				position: absolute;
				top: 16px;
				left: 16px;
				width: 14px;
				height: 14px;
				background-image: url(../img/icon/reply.svg);
				background-repeat: no-repeat;
				background-position: center;
				background-size: contain;

				@media (min-width: @min-lg) {
					top: 20px;
					left: 20px;
					width: 18px;
					height: 18px;
				}
			}
		}
	}
}

.comments__author {
	display: flex;
	align-items: center;
	font-size: 12px;
	line-height: 20px;
	color: rgba(0, 0, 0, 0.5);

	@media (min-width: @min-md) {
		font-size: 14px;
		line-height: 20px;
	}

	& img {
		margin-right: 10px;
		border-radius: 50%;

		@media (min-width: @min-md) {
			width: 64px;
			height: 64px;
			margin-right: 16px;
		}
	}

	& b {
		color: #000000;
		letter-spacing: 0.01em;
	}
}

.comments__footer {
	display: flex;
	align-items: center;

	&::after {
		content: '';
		display: block;
		width: 12px;
		height: 2px;
		background-image: url(../img/icon/menu.svg);
		background-repeat: no-repeat;
		background-position: center;
		background-size: contain;
	}
}

.comments__icon {
	position: relative;
	display: flex;
	align-items: center;
	margin-right: 24px;
	padding: 0;
	font-size: 12px;
	line-height: 20px;
	font-family: inherit;
	color: rgba(0, 0, 0, 0.5);
	background-color: transparent;
	border: 0;
	transition: color var(--transition-duration) var(--transition-function);
	appearance: none;

	&:hover {
		&::before {
			opacity: 0;
		}

		&::after {
			opacity: 1;
		}
	}

	&::before,
	&::after {
		content: '';
		display: block;
		width: 14px;
		height: 15px;
		margin-right: 5px;
		background-repeat: no-repeat;
		background-position: center;
		background-size: contain;
		transition: opacity var(--transition-duration) var(--transition-function);
	}

	&::after {
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0;
	}

	&--like {
		&::before {
			background-image: url(../img/icon/like.svg);
		}

		&::after {
			top: 2px;
			background-image: url(../img/icon/like-fill.svg);
		}
	}

	&--dislike {
		&::before {
			background-image: url(../img/icon/dislike.svg);
		}

		&::after {
			background-image: url(../img/icon/dislike-fill.svg);
		}
	}
}

